<template>
  <div class="b">
    <!--<el-button type="text" @click="DialogVisible = true" class="el-button"><img src="~assets/store.png"/></el-button>-->

      <img src="~assets/store.png" class="a" @click="DialogVisible = true"/>

    <el-dialog
      title=""
      :visible.sync="DialogVisible"
      width="75%"
      @close=" dailogClose"
      class="dailog"
      center>
      <el-form ref="FormRef" :model="storeForm" label-width="80px" :rules="dailogFormRules">
        <el-form-item label="" prop="name">
          <el-input v-model="storeForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="address">
          <el-select v-model="storeForm.address" placeholder="">
            <el-option label="3" value="shanghai"></el-option>
            <el-option label="2" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-checkbox-group v-model="storeForm.type">
            <el-checkbox label="" name="snack"></el-checkbox>
            <el-checkbox label="" name="goods"></el-checkbox>
            <el-checkbox label="" name="help"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input type="textarea" v-model="storeForm.desc"></el-input>
        </el-form-item>
        <el-form-item label="logo" prop="logo">
            <load-img @LoadImg="loadImg"></load-img>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="DialogVisible = false"> </el-button>
    <el-button type="primary" @click="onSubmit"></el-button>
    </span>
    </el-dialog>
  </div>
</template>

<script>
  import LoadImg from 'other/LoadImg'
    export default {
        name: "Dailog",
      components:{
        LoadImg
      },
      data(){
          return{
            DialogVisible:false,
            storeForm: {
              name: '',
              type: '',
              address: '',
              desc: '',
              imgUrl: ''
            },
              dailogFormRules:{
                name:[
                  { required: true, message: '', trigger: 'blur' },
                  { min: 1, max: 6, message: ' 1 6 ', trigger: 'blur' }
                ],
                address:[
                  { required: true, message: '', trigger: 'blur' },
                ],
                type:[
                  { required: true, message: '', trigger: 'blur' },
                ],
                desc:[
                  { required: true, message: '', trigger: 'blur' },
                  { min: 2, max: 30, message: ' 2 30 ', trigger: 'blur' }
                ],
                logo:[
                  { required: true, message: 'logo', trigger: 'blur' },
                ],
              }
            }

      },
      methods: {
        //
        dailogClose(){
          this.$refs.FormRef.resetFields()
        },
        //
        onSubmit() {
          this.$refs.FormRef.validate(valid => {
              if(valid)
              {
                this.DialogVisible = !this.DialogVisible
                this.$message.success("")
                console.log("")
              }
          })
        },
        //
        loadImg(url){
          this.storeForm.imgUrl=url
       }
      }
    }
</script>

